import React, { Component } from 'react';

class NavigationItem extends Component {
  render() {
    return (
        <button className="btn btn-secondary btn-outline-light" type="button" data-toggle="collapse" data-target="#collapseContent1" aria-expanded="false">
            <i className="far {this.props.navigation.icon} fa-3x"></i>
            <div className="title">{this.props.navigation.title}</div>
            <div className="plus">+</div>
        </button>
    );
  }
}

export default NavigationItem;